import React from 'react'
import Title from '../components/Title'
import ContactButton from '../components/ContactButton'

const Experiences = [
  {
    date: '4 Jun 2019 - present',
    position: 'Software Engineer',
    company: 'Internet Thailand Public Co., Ltd.'
  },
  {
    date: '7 Jan. - 3 May. 2019',
    position: 'Android Developer (Internship)',
    company: 'Rise Plus Technology Co., Ltd.'
  },
  {
    date: '4 Jun. - 3 Aug. 2018',
    position: 'Android Developer (Internship)',
    company: 'DigiX Technology Co., Ltd.'
  }
]

const Activities = [
  {
    date: '2019',
    detail: 'CompTIA Cloud Essentials no. COMP001021515150'
  },
  {
    date: '2018',
    detail: 'Thailand Professional Qualification Institute (TPQI) no. PQCN-ICT-SWA-3-251200-A-61-000201'
  },
  {
    date: '2017',
    detail: 'Information Technology Professionals Examination (ITPE) no.THIP17A0044'
  },
  {
    date: '2017',
    detail: 'Agile for Software Development Workshop , Instructor  Mr.Prathan Dansakulcharoenkit'
  }
]

const TitleH2 = (props) => {
  const { text } = props
  return (
    <div className="flex flex-row">
      <div className="w-1 my-1 mr-4 bg-primary"></div>
      <h2 className="text-base tracking-wide text-white uppercase md:text-lg">{text}</h2>
    </div>
  )
}

const PersonalInfo = (props) => {
  const { title, detail } = props
  return (
    <div className="flex flex-row align-middle">
      <div className="text-sm text-gray-600 md:text-base">
        {title || 'title'}
      </div>
      <span className="px-2 text-sm text-gray-600"> : </span>
      <div className="text-sm text-gray-500 md:text-base">
        {detail || 'detail'}
      </div>
    </div>
  )
}

const CardExperience = (props) => {
  const { date, position, company } = props.experience
  return (
    <div className="flex flex-col py-1">
      <div className="text-xs leading-3 text-gray-600 md:text-sm">{date || 'Date'}</div>
      <div className="flex flex-col md:flex-row">
        <div className="text-sm text-white md:text-base">{position || 'Position'}</div>
        <div className="text-sm text-gray-500 md:pl-2 md:text-base">   {company}</div>
      </div>
    </div>
  )
}

const CardActivity = (props) => {
  const { date, detail } = props.activity
  return (
    <div className="flex flex-col py-1">
      <div className="text-xs leading-3 text-gray-600 md:text-sm">{date || 'Date'}</div>
      <div className="flex flex-row">
        <div className="text-sm text-gray-500 md:text-base">{detail}</div>
      </div>
    </div>
  )
}

