export default function Resume() {
  return (
    <div className="flex flex-col px-10 pt-10 pb-4 my-6 bg-gray-700">
      <div data-aos="fade-up" className="flex justify-center pt-2 pb-4">
        <Title text="Resume" />
      </div>
      <CardContact />
      <div data-aos="fade-up" className="py-2 md:py-4">
        <TitleH2 text="Personal Information" />
        <div className="flex flex-col">
          <PersonalInfo title="Name" detail="Jirayu  Pharang" />
          <PersonalInfo title="Age" detail="23 years" />
          <PersonalInfo title="Nationality" detail="Thai" />
          <PersonalInfo title="Language" detail="Thai (Native), English (Intermediate)" />
        </div>
      </div>
      <div data-aos="fade-up" className="py-2 md:py-4">
        <TitleH2 text="SKILLS" />
        <div className="flex flex-col">
          <p className="text-sm text-gray-500 md:text-base">Java, Kotlin</p>
          <p className="text-sm text-gray-500 md:text-base">Javascript/React Native, React.js</p>
          <p className="text-sm text-gray-500 md:text-base">Node.js, MongoDB</p>
        </div>
      </div>
      <div data-aos="fade-up" className="py-2 md:py-4">
        <TitleH2 text="EDUCATION" />
        <div className="felx-col">
          <p className="text-sm text-gray-500 whitespace-pre-wrap md:text-base ">
            Bachelor of Science ,Information and Communication Technology
        </p>
          <div className="flex flex-row">
            <p className="text-sm text-gray-500 whitespace-pre-wrap md:text-base">Khon Kaen university ,Khon Kaen ,Thailand
            <span className="text-xs text-gray-600 md:text-sm">
                {` ( 2015- 2019 )`}
              </span>
            </p>
          </div>
        </div>
      </div>
      <div data-aos="fade-up" className="py-2 md:py-4">
        <TitleH2 text="WORK EXPERIENCES" />
        {
          Experiences.map((doc, index) => {
            return (
              <CardExperience key={index} experience={doc} />
            )
          })
        }
      </div>
      <div data-aos="fade-up" className="py-2 md:py-4">
        <TitleH2 text="Activity and Certification" />
        {
          Activities.map((doc, index) => {
            return (
              <CardActivity key={index} activity={doc} />
            )
          })
        }
      </div>
    </div>
  )
}
