import React from 'react'

export default function Title(props) {
  const { text } = props
  return (
    <div className="flex flex-row">
      <div className="w-1 my-1 mr-4 bg-primary"></div>
      <h2 className="text-2xl tracking-wide text-white">{text}</h2>
    </div>
  )
}

Title.defaultProps = {
  text: "Title"
}
